<template>
  <div class="main-page">
    <div
      class="cover-section"
      :style="{
        backgroundImage: `url(${publicPath}img/${content.coverImage}.svg)`
      }"
    >
      <div class="container">
        <div class="content-wrapper offset-lg-3 col-lg-6">
          <h3 v-html="content.subtitle"></h3>
          <h1 v-html="content.title"></h1>
        </div>
      </div>
    </div>
    <div class="nav">
      <div
        :class="['element', { selected: element === currentSection }]"
        v-for="(element, index) in content.nav"
        :key="index"
        @click="scrollTo(element)"
      >
        <h3>{{ element }}</h3>
      </div>
    </div>

    <div
      v-for="(section, sectionIndex) in content.sections"
      :key="sectionIndex"
    >
      <div class="background" v-if="section.type === 'background'">
        <div
          class="container"
          v-for="(section, subSectionIndex) in section.sections"
          :key="subSectionIndex"
        >
          <div class="section" v-if="section.type === 'paragraph'">
            <h2 class="offset-lg-3 col-lg-6" v-html="section.title"></h2>
            <p class="offset-lg-3 col-lg-6" v-html="section.text"></p>
          </div>
          <div
            class="section offset-lg-2 col-lg-8"
            v-if="section.type === 'image' && section.format === 'jpg'"
          >
            <img
              :src="publicPath + 'img/' + section.name + '.' + section.format"
            />
          </div>
          <div class="dida" v-if="section.type === 'dida'">
            <h3 class="offset-lg-3 col-lg-6" v-html="section.content"></h3>
          </div>
        </div>
      </div>
      <div class="container">
        <div
          v-if="section.type === 'anchor'"
          :id="section.content"
          v-observe-visibility="
            (isVisible, entry) =>
              visibilityChanged(isVisible, entry, section.content)
          "
        ></div>

        <div class="section" v-if="section.type === 'paragraph'">
          <h2 class="offset-lg-3 col-lg-6" v-html="section.title"></h2>
          <p class="offset-lg-3 col-lg-6" v-html="section.text"></p>
        </div>
        <div
          class="section offset-lg-3 col-lg-6"
          v-if="section.type === 'image' && section.format === 'svg'"
        >
          <img
            :src="publicPath + 'img/' + section.name + '.' + section.format"
          />
        </div>
      </div>
    </div>
    <div class="container">
      <h3 class="credits offset-lg-3 col-lg-6">
        designed and developed by Giulia Zerbini. Course Description: Zona
        Kostic.
      </h3>
    </div>
  </div>
</template>

<script>
import content from "../assets/content/content.json";
export default {
  data() {
    return {
      content: content,
      publicPath: process.env.BASE_URL,
      currentSection: "info",
      show: true,
      isVisible: true,
      throttle: 0,
      threshold: 0
    };
  },
  methods: {
    scrollTo(hash) {
      location.hash = "#" + hash;
      this.currentSection = hash;
    },
    visibilityChanged: function(isVisible, entry, value) {
      this.isVisible = isVisible;
      if (isVisible) {
        this.currentSection = value;
      }
    }
  }
};
</script>

